import React, { useState, useEffect } from 'react';
import { formatAmount } from '../../lib/legacyOptions';
import { ImSpinner2 } from 'react-icons/im';
import { USD_DECIMALS, INCREASE, STOP } from '../../lib/legacyOptions';
import { Trans } from '@lingui/macro';
import '../../../gmx-test/components/Exchange/PositionsItem.scss';
import cx from "classnames";
import { OPTIONS_DECIMALS } from '../../../../components/utils/constants';
import { formatAmountFree, ARBITRUM } from '../../lib/legacyOptions';
import { getTokenInfo } from '../../../gmx-test/lib/legacy';
import { getTokenBySymbol } from '../../config/Tokens';
import { fromBigNumber, msToTimeLeft } from '../../../../components/utils/utils';

const PositionsItem = (props) => {
	const {
		position,
		onPositionClick,
		infoTokens,
		closePosition,
		isLarge,
	} = props;

	const markPrice = getTokenInfo(infoTokens, getTokenBySymbol(ARBITRUM, position.token).address).maxPrice;
	const expiresIn = position.expiry - Date.now();
	
	let delta = 0, deltaStr = '...', hasPositionProfit = false;

	if (position.pnl) {
		const { unrealizedPnl, unrealizedPnlPercentage } = position.pnl;
		delta = unrealizedPnl;
		
		deltaStr = `
			${unrealizedPnl.lt(0) ? '-' : ''}$${Math.abs(fromBigNumber(unrealizedPnl, OPTIONS_DECIMALS)).toFixed(2)}
			(${formatAmount(unrealizedPnlPercentage, OPTIONS_DECIMALS - 2, 2, true)}%)
		`;
		hasPositionProfit = unrealizedPnl.gt(0);
	}

	const marketImg = require(`../../../../img/icon-${position.market}_sm.svg`).default;
	
	return (
		<>
			{isLarge ?
				<tr>
					<td className="clickable" onClick={() => onPositionClick(position)}>
						<div className="Exchange-list-title">
							{position.token}
							{position.hasPendingChanges && <ImSpinner2 className="spin position-loading-icon" />}
						</div>
						<div className="Exchange-list-info-label">
							<img className='market-logo' src={marketImg} alt={`${position.market} icon`} />
							<span className={cx({ positive: position.isCall, negative: !position.isCall })}>
								{position.isCall ? "Call" : "Put"}
							</span>
						</div>
					</td>
					<td>
						{formatAmountFree(position.size, OPTIONS_DECIMALS, 3)} {position.token}
					</td>
					<td>
						${formatAmount(position.strikePrice, OPTIONS_DECIMALS, 2, true)}
					</td>
					<td style={{width: 135}}>
						${formatAmount(markPrice, USD_DECIMALS, 2, true)}
					</td>
					<td>
						<div>
							${formatAmount(position.premium, OPTIONS_DECIMALS, 2, true)}
						</div>
					</td>
					<td>
						<div style={{ width: 100, display: 'block' }}>
							{delta && (
								<div
									className={cx("Exchange-list-info-label", {
										positive: hasPositionProfit,
										negative: !hasPositionProfit,
										muted: delta.eq(0),
									})}
								>
									{deltaStr}
								</div>
							)}
						</div>
					</td>
					<td>
						{msToTimeLeft(expiresIn)}
					</td>
					<td className="td-btn pos-relative" style={{ width: 110, textAlign: 'center' }}>
						<button
							className="Exchange-list-action"
							onClick={() => {
								closePosition(position);
							}}
							disabled={position.size.eq(0)}
						>
							Close
						</button>
					</td>
				</tr>
			:
			<div className="App-card">
				<div className="App-card-divider"></div>
				<div className="App-card-content">
					<div className="App-card-row">
						<div className="label">
							<Trans>Position</Trans>
						</div>
						<div>
							<div className="Exchange-list-token">
								{position.hasPendingChanges && <ImSpinner2 className="spin position-loading-icon" style={{marginRight: 10}} />}
								{position.token}
								</div>
							<img className='market-logo' src={marketImg} alt={`${position.market} icon`} />
							<span
								className={cx("Exchange-list-side", {
									positive: position.isCall,
									negative: !position.isCall,
								})}
							>
								{position.isCall ? `Call` : `Put`}
							</span>
						</div>
					</div>
				</div>
				<div className="App-card-divider"></div>
				<div className="App-card-content">
					<div className="App-card-row">
						<div className="label">
							<Trans>Amount</Trans>
						</div>
						<div>
							{formatAmountFree(position.size, OPTIONS_DECIMALS, 3)} {position.token}
						</div>
					</div>
					<div className="App-card-row">
						<div className="label">
							<Trans>Strike Price</Trans>
						</div>
						<div>
							${formatAmount(position.strikePrice, OPTIONS_DECIMALS, 2, true)}
						</div>
					</div>
					<div className="App-card-row">
						<div className="label">
							<Trans>Current Price</Trans>
						</div>
						<div>
							${formatAmount(markPrice, USD_DECIMALS, 2, true)}
						</div>
					</div>
					<div className="App-card-row">
						<div className="label">
							Premium Paid
						</div>
						<div>
							${formatAmount(position.premium, OPTIONS_DECIMALS, 2, true)}
						</div>
					</div>
					<div className="App-card-row">
						<div className="label">
							PNL (ROE %)
						</div>
						<div>
							{delta && (
								<div
									className={cx("Exchange-list-info-label", {
										positive: hasPositionProfit,
										negative: !hasPositionProfit,
										muted: delta.eq(0),
									})}
								>
									{deltaStr}
								</div>
							)}
						</div>
					</div>
					<div className="App-card-row App-card-row_edit-btn">
						<div className="label">
							Expires In
						</div>
						<div>
							{msToTimeLeft(expiresIn)}
						</div>
					</div>
				</div>
				<div className="App-card-divider"></div>
				<div className="App-card-options">
					<div className="App-button-option pos-relative">
						<button
							className="App-button-option App-card-option"
							disabled={position.size.eq(0)}
							onClick={() => {
								closePosition(position);
							}}
						>
							Close
						</button>
					</div>
				</div>
			</div>}
		</>
	);
};

export default PositionsItem;