import React, { useState, useEffect, useContext } from 'react';
import { Routes, Route, Navigate, useNavigate, useLocation } from "react-router-dom";
import { DDL_POOL, USDC } from './utils/contracts';
import { UserStatsContext } from '../context/context';
import { ethers } from 'ethers';
import GMXInterface from '../views/gmx-test/App/App';
import { connectWallet } from './utils/wallet';
import { HOMEPAGE_HREF } from './utils/constants';
import Options from './../views/options/App/App';
import { useLocalStorageByChainId } from './../views/gmx-test/lib/legacy';
import { ARBITRUM } from './../views/options/lib/legacyOptions';


const AppRouter = ({ walletAddress, setWalletAddress }) => {
	const [path, setPath] = useLocalStorageByChainId(ARBITRUM, "path", HOMEPAGE_HREF);

	const nav = useNavigate();
	const loc = useLocation();
	useEffect(() => {
		if (!loc.pathname) {
			nav(path);
		}
	}, []);

	useEffect(() => {
		setPath(loc.pathname);
	}, [loc.pathname]);

	
	return (
		<Routes>
			{<Route path="/perpetuals" element={
				<GMXInterface
					connectWallet={() => {
						connectWallet(setWalletAddress)
					}}
					walletAddress={walletAddress} />
			} />}
			<Route path="/options" element={
				<Options
					connectWallet={() => {
						connectWallet(setWalletAddress)
					}}
					walletAddress={walletAddress} />
			} />
			<Route
				path="*"
				element={<Navigate to={path} replace />}
			/>
		</Routes>
	);
};

export default AppRouter;