import React, { useState } from "react";
import { Trans, t } from "@lingui/macro";

import {
  helperToast,
  CALL,
  PUT,
} from "../../lib/legacyOptions";
import PositionsItem from './PositionsItemOptions';
import ClosePositionModal from './ClosePositionModal';

export default function PositionsList(props) {
  const {
    positions,
    infoTokens,
    setMarket,
    positionsLoading,
  } = props;

  const hasPositions = Boolean(positions && positions.length);

  const [closeModalVisible, setCloseModalVisible] = useState(false);
  const [position, setPosition] = useState(null);

  const closePosition = (position) => {
    setPosition(position);
    setCloseModalVisible(true);
  }
  
  const onPositionClick = (position) => {
    helperToast.success(`${position.isCall ? "Call" : "Put"} ${position.token} market selected`);
    setMarket(position.isCall ? CALL : PUT, position.token);
  };

  return (
    <div className="PositionsList">
      <div className="Exchange-list small">
        <div>
          {!hasPositions && positionsLoading && (
            <div className="Exchange-empty-positions-list-note App-card Exchange-list-empty-note">
              Loading...
            </div>
          )}
          {!hasPositions && !positionsLoading && (
            <div className="Exchange-empty-positions-list-note App-card Exchange-list-empty-note">
              No open positions
            </div>
          )}
          {hasPositions && positions.map((position) => {
            return (
              <PositionsItem
                key={position.strategy}
                position={position}
                onPositionClick={onPositionClick}
                infoTokens={infoTokens}
                closePosition={closePosition}
                isLarge={false}
              />
            );
          })}
        </div>
      </div>
      <table className="Exchange-list large App-box" cellSpacing={0}>
        <tbody>
          {hasPositions && (
            <tr className="Exchange-list-header">
              <th>
                Position
              </th>
              <th>
                Amount
              </th>
              <th>
                Strike Price
              </th>
              <th>
                Current Price
              </th>
              <th>
                Premium Paid
              </th>
              <th>
                PNL (ROE %)
              </th>
              <th>
                Expires In
              </th>
              <th></th>
            </tr>
          )}
          {!hasPositions && positionsLoading && (
            <tr>
              <td colSpan="15" className="Exchange-list-empty-note">
                Loading...
              </td>
            </tr>
          )}
          {!hasPositions && !positionsLoading && (
            <tr>
              <td colSpan="15" className="Exchange-list-empty-note">
                No open positions
              </td>
            </tr>
          )}
          {hasPositions && positions.map((position) => {
            return (
              <PositionsItem
                key={position.strategy}
                position={position}
                onPositionClick={onPositionClick}
                infoTokens={infoTokens}
                closePosition={closePosition}
                isLarge={true}
              />
            );
          })}
        </tbody>
      </table>

      <ClosePositionModal
        visible={closeModalVisible}
        setVisible={setCloseModalVisible}
        position={position}
        setPosition={setPosition}
      />
    </div>
  );
}
