import React, { useState, useEffect } from 'react';
import Modal from '../../../../components/UI/modal/Modal';
import { sepToNumber } from '../../../../components/utils/sepThousands';
import { BigNumber } from 'ethers';
import NumberInput_v3 from '../../../../components/UI/input/NumberInput_v3';
import { errAlert, notifySuccess } from '../../../../components/utils/notifications';
import { expandDecimals, formatAmount } from '../../../gmx-test/lib/legacy';
import { OPTIONS_DECIMALS, tokenDecimals } from '../../../../components/utils/constants';
import { executePromise, fromBigNumber, msToTimeLeft } from '../../../../components/utils/utils';
import { format } from 'date-fns';
import cx from 'classnames';
import './PositionModal.scss';
import { CALL, PUT } from './../../lib/legacyOptions';
import { separateThousands } from './../../../../components/utils/sepThousands';
import useDebounce from './../../../../hooks/useDebounce';
import { USDC } from '../../../../components/utils/contracts';
import { approveUSDC } from './../../../../components/utils/contracts';

const BuyPositionModal = (props) => {
	const {
		visible,
		setVisible,
		position,
		setPosition,
		library,
		walletAddress,
	} = props;

	const [sizeValue, setSizeValue] = useState("");
	const size = expandDecimals(BigNumber.from((sepToNumber(sizeValue) * 10**6).toFixed(0)), 12);

	const [premium, setPremium] = useState(undefined);
	const onSizeValueChange = useDebounce((value) => {
		const size = expandDecimals(BigNumber.from((Number(value) * 10**6).toFixed(0)), 12);
		
		position.getPremium(size)
			.then(res => {
				setPremium(res);
			})
	}, 500);

	useEffect(() => {
		if (position) {
			setSizeValue(
				separateThousands(
					fromBigNumber(position.initSize, tokenDecimals[position.token])
				)
			);

			setPremium(position.cost);
		}
	}, [position]);

	const [isSubmitting, setIsSubmitting] = useState(false);
	const [isApproving, setIsApproving] = useState(false);

	const onPrimaryClick = () => {
		if (!position.USDCApproved) {
			approveUSDC(position.contractAddress, setIsApproving, () => {
				position.USDCApproved = true;
			});
		} else {
			executePromise(position.buy, [size], "Position opened!", setIsSubmitting, () => setVisible(false));
		}
	}

	const getError = () => {
		if (!position) {
			return "ERR! No position";
		}

		if (!sizeValue) {
			return "Enter amount";
		}

		if (isApproving) {
			return "Approving USDC...";
		}

		if (isSubmitting) {
			return "Opening...";
		}
	}
	const getBtnText = () => {
		if (!position?.USDCApproved) {
			return `Allow to use your USDC`;
		}
		
		const error = getError();
		if (error) {
			return error;
		}
		return `Buy ${position.isCall ? CALL : PUT} option`;
	}
	const isBtnEnabled = () => {
		const hasError = Boolean(getError());
		if (hasError) {
			return false;
		}
		return true;
	}

	const resetModal = () => {
		// animation delay
		setTimeout(() => {
			setSizeValue("");
			setPosition(null);
			setPremium(undefined);
		}, 300);
	}
	
	const marketImg = position
		? require(`../../../../img/icon-${position.market}.svg`).default
		: "";

	return (
		<Modal
			className={"BuyOptionModal PositionModal"}
			visible={visible}
			setVisible={setVisible}
			resetModal={resetModal}
		>
			<h1 className="modal__title">Buy Position</h1>
			{position &&
				<div className="modal__body">
					<div className="Exchange-info-row">
						<div className="Exchange-info-label">
							Market
						</div>
						<div className="align-right PositionModal__market-logo">
							<img src={marketImg} alt={`${position.market} logo`} />
						</div>
					</div>
					<div className="Exchange-info-row">
						<div className="Exchange-info-label">
							Type
						</div>
						<div className="align-right">
							{position.type}
						</div>
					</div>
					<div className="Exchange-info-row">
						<div className="Exchange-info-label">
							Strike
						</div>
						<div className="align-right">
							${formatAmount(position.strike, OPTIONS_DECIMALS, 2, true)}
						</div>
					</div>
					<div className="Exchange-info-row">
						<div className="Exchange-info-label">
							Expires In
						</div>
						<div className="align-right">
							{msToTimeLeft(position.expiry - Date.now())}
						</div>
					</div>
					<div className="Exchange-info-row">
						<div className="Exchange-info-label">
							Expiration Date
						</div>
						<div className="align-right">
							{format(position.expiry, 'MM/dd/yy, hh:mm a')}
						</div>
					</div>
					<NumberInput_v3
						title='Amount'
						value={sizeValue}
						setValue={setSizeValue}
						maxValue={fromBigNumber(position.size)}
						icon={require(`../../../../img/icon-${position.token}.svg`).default}
						iconAlt={`${position.token} icon`}
						onChange={(value) => {
							setPremium(undefined);
							onSizeValueChange(value);
						}}
					/>
					<div className="Exchange-info-row hlight">
						<div className="Exchange-info-label">
							Price Per Option
						</div>
						<div className="align-right">
							${formatAmount(position.price, OPTIONS_DECIMALS, 2, true)}
						</div>
					</div>
					<div className="Exchange-info-row hlight">
						<div className="Exchange-info-label">
							Premium Paid
						</div>
						<div className="align-right">
							${formatAmount(premium, OPTIONS_DECIMALS, 2, true)}
						</div>
					</div>
				</div>}
			
			<button 
				className="modal__btn btn btn_hlight"
				onClick={onPrimaryClick}
				disabled={!isBtnEnabled()}
			>
				{getBtnText()}
			</button>
		</Modal>
	);
};

export default BuyPositionModal;